@import url(https://rsms.me/inter/inter-ui.css);
html { font-family: 'Inter UI', sans-serif; }
@supports (font-variation-settings: normal) {
  html { font-family: 'Inter UI var alt', sans-serif; }
}

html {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color:black;
  font-family: 'Inter UI var alt', sans-serif;
}

#root {
  margin: 0;
  padding: 0;
  height: 100%;
}

canvas {
  margin: 0;
  padding: 0;
  height: 100%;
}


.App {
  height: 100%;
  display: block;
  
}

#drawing {
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
}

svg {
  display: block;
  background-color: black;
}

#network {
  height: 100%;
  position: absolute;
}

.node {
  position: absolute;
  color: white;
  text-align: center;
  width: 20px;
  font-size: 20px;
  font-weight: medium;
  opacity: 0.2;
}

.color {
  position: absolute;
  bottom: 20px;
  width: 30px;
  height: 4px;
}
